@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500;600;700;900&display=swap');
:root {
    --dark-brown: #593C1C;
    --brown: #A97B47;
    --red: #C82525;
    --gray: #909090;
    --blue: #315FD8;
    --isabelline: #F7F0E9;
    --dark-isabelline: #D8C1A8;
    --very-light-gray: #F5F5F5;
    --yellow: #FFC700;
    --border-radius: 10px;
    font-size: 80%;
}

.brand-tf {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    color: var(--brown);
}

.brand-icon {
    width: 100px;
    height: 100px;
}

#filter-box {
    background: var(--isabelline);
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25);
    border-radius: var(--border-radius);
}

.min-w-100 {
    min-width: 100px;
}
