.below-header {
    padding-top: 80px;
}

body {
    background-color: #F5F5F5;
    position: relative;
    min-height: 100vh;
    padding-bottom: 50px; /* footer height*/
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

#product-details {
    grid-template-columns: auto;
    grid-template-rows: [row1-start] 25% [row1-end] 100px [third-line] auto [last-line];
}

.size-icon {
    width: 24px;
    height: 24px;
}

.size-icon-sm {
    width: 15px;
    height: 15px;
}

.text-gray {
    color: var(--gray);
    font-size: 0.85rem;
}

.add-to-cart-card {
    background-color: white;
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25);
}

.rating-star {
    cursor: pointer;
}

.comment-card {
    background-color: white;
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25);
}

.white-card {
    border-radius: var(--border-radius);
    background-color: white;
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25);
}

.text-box {
    min-height: 65px;
    resize: none;
    background-color: var(--very-light-gray);
    border-radius: var(--border-radius);
    border-width: 0;
}

.icon-grid {
    display: grid;
    grid-template-columns: 30px auto;
    align-items: center;
    grid-auto-rows: minmax(34px, max-content);
}

.col-icon {
    justify-self: center;
}

.text-credit {
    font-size: 4rem;
    font-weight: 600;
}

.text-box-add-credit {
    min-height: 30px;
    background-color: white;
    border-radius: var(--border-radius);
    border-width: 1px;
    text-align: center;
    font-size: 1.2rem;
    color: var(--gray);
}

.text-box-discount {
    min-height: 30px;
    background-color: white;
    border-radius: var(--border-radius);
    border-width: 1px;
    width: 120px;
    text-align: center;
    font-size: 1rem;
    color: var(--bs-black);
}

input[type="number"]::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
}

.thumbnail-img {
    object-fit: cover;
    width: 75px;
    height: 75px;
    border-radius: var(--border-radius);
}

.btn-wide {
    width: 400px;
    max-width: 100%;
}

.bg-isabelline {
    background-color: var(--isabelline);
}

.no-outline {
    outline: none;
}

.round-border {
    border-radius: var(--border-radius);
}

.search-box {
    background-color: transparent;
    height: 100% !important;
    font-size: 1rem;
    color: var(--gray);
}

.search-box::placeholder {
    color: var(--gray);
}

.shadow {
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25) !important;
}

.product-card {
    box-shadow: 0px 4px 4px rgba(208, 208, 208, 0.25);
    width: 250px;
    border: none;
}

.commodity-card-flow {
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(auto-fit, 260px);
    justify-content: center
}

.star-off {
    filter: grayscale(100%);
}

.filter-green {
    /* to black */
    /*filter: invert(1);*/
    /* or to blue */
    filter: invert(0.5) sepia(1) saturate(5) hue-rotate(90deg);
}
.filter-red {
    /* to black */
    /*filter: invert(1);*/
    /* or to blue */
    filter: invert(0.5) sepia(1) saturate(5) hue-rotate(290deg);
}