:root {
    --bs-border-radius: .5rem;
    --bs-link-color: #484848;
    --bs-primary-text-emphasis: var(--dark-brown);
    --bs-primary-text: var(--dark-brown);
    --bs-primary-rgb: 89, 60, 28;
    --bs-secondary-text: var(--brown);
    --bs-secondary-rgb: 169, 123, 71;
    --bs-warning: var(--yellow);
    --bs-warning-rgb: 255, 199, 00;
    --bs-danger: var(--red);
    --bs-danger-rgb: 200, 37, 37;
}

.btn {
    --bs-btn-font-family: Inter, sans-serif;
    --bs-btn-font-weight: 600;
    --bs-btn-border-color: transparent;
    --bs-btn-border-radius: var(--border-radius);
    --bs-btn-hover-border-color: transparent;
}

.btn-primary {
    --bs-btn-color: #fff;
    --bs-btn-bg: var(--dark-brown);
    --bs-btn-border-color: transparent;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: var(--brown);
    --bs-btn-hover-border-color: transparent;
    --bs-btn-focus-shadow-rgb: 49, 132, 253;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: var(--brown);
    --bs-btn-active-border-color: transparent;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: var(--dark-brown);
    --bs-btn-disabled-bg: var(--isabelline);
    --bs-btn-disabled-border-color: transparent;
}

.btn-secondary {
    --bs-btn-color: var(--brown);
    --bs-btn-bg: var(--isabelline);
    --bs-btn-border-color: transparent;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: var(--brown);
    --bs-btn-hover-border-color: transparent;
    --bs-btn-focus-shadow-rgb: 130, 138, 145;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #565e64;
    --bs-btn-active-border-color: transparent;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #6c757d;
    --bs-btn-disabled-border-color: transparent;
}

.btn-outline-primary {
    --bs-btn-color: var(--dark-brown);
    --bs-btn-border-color: var(--dark-brown);
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: var(--brown);
    --bs-btn-hover-border-color: var(--brown);
    --bs-btn-focus-shadow-rgb: 13, 110, 253;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: var(--dark-brown);
    --bs-btn-active-border-color: var(--brown);
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: var(--gray);
    --bs-btn-disabled-bg: var(--very-light-gray);
    --bs-btn-disabled-border-color: var(--gray);
    --bs-gradient: none;
}

.outline-primary {
    --bs-btn-color: var(--dark-brown);
    --bs-btn-border-color: var(--dark-brown);
    --bs-btn-hover-color: var(--bs-btn-color);
    --bs-btn-hover-bg: transparent;
    --bs-btn-hover-border-color: var(--dark-brown);
    --bs-btn-focus-shadow-rgb: 13, 110, 253;
    --bs-btn-active-color: var(--bs-btn-color);
    --bs-btn-active-bg: transparent;
    --bs-btn-active-border-color: var(--bs-btn-border-color);
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: var(--gray);
    --bs-btn-disabled-bg: var(--very-light-gray);
    --bs-btn-disabled-border-color: var(--gray);
    --bs-gradient: none;
    cursor: default;
}

.btn-outline-secondary {
    --bs-btn-color: var(--gray);
    --bs-btn-border-color: var(--gray);
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: var(--gray);
    --bs-btn-hover-border-color: var(--gray);
    --bs-btn-focus-shadow-rgb: 13, 110, 253;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: var(--gray);
    --bs-btn-active-border-color: var(--gray);
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: var(--gray);
    --bs-btn-disabled-bg: transparent;
    --bs-btn-disabled-border-color: var(--gray);
    --bs-gradient: none;
}

.btn-icon {
    --bs-btn-color: var(--dark-brown);
    --bs-btn-border-color: transparent;
    --bs-btn-hover-color: var(--brown);
    --bs-btn-hover-bg: transparent;
    --bs-btn-hover-border-color: transparent;
    --bs-btn-focus-shadow-rgb: 13, 110, 253;
    --bs-btn-active-color: var(--bs-btn-color);
    --bs-btn-active-bg: transparent;
    --bs-btn-active-border-color: var(--bs-btn-border-color);
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: var(--gray);
    --bs-btn-disabled-bg: var(--very-light-gray);
    --bs-btn-disabled-border-color: var(--gray);
    --bs-gradient: none;
    cursor: pointer;
}

.btn-radio {
    --bs-btn-color: var(--gray);
    --bs-btn-bg: transparent;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: var(--brown);
    --bs-btn-active-border-color: transparent;
}

/* make switch bigger */
.form-switch .form-check-input {
    height: 1.5rem;
    width: 2.7rem;
    border-color: transparent;
}

/* disable changing of background in focus */
.form-switch .form-check-input:focus {
    --bs-form-switch-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 0.25%29'/%3e%3c/svg%3e");
}
.form-switch .form-check-input:checked:focus {
    background-position: right center;
    --bs-form-switch-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
}

.form-check-input:checked {
    background-color: #88ab00;
    border-color: transparent;
}

.dropdown-toggle {
    --bs-btn-font-weight: 400;
    --bs-btn-color: var(--dark-brown);
    --bs-btn-bg: var(--dark-isabelline);
    --bs-btn-hover-color: #000;
    --bs-btn-active-color: #000;
    --bs-btn-hover-bg: var(--dark-isabelline);
    --bs-btn-active-bg: var(--dark-isabelline);
}

.dropdown-menu {
    --bs-btn-font-weight: 400;
    --bs-dropdown-color: var(--dark-brown);
    --bs-dropdown-bg: var(--dark-isabelline);
}



